html{
  background:black;
}
.App-header {
  height: 100%;
  width: 100%;
}

.App-title {
  font-size: 1.5em;
  max-height:500px;
}

.App-intro {
  font-size: large;
}
.previewComponent {
  background:#ccc;
  -webkit-box-shadow: 0px 3px #ccc;
          box-shadow: 0px 3px #ccc;
  border: 2px solid whitesmoke;
}

p{
  font-family: 'Slabo 27px', serif;
  position: absolute;
  background: -webkit-gradient(linear, left top, right bottom, from(black), to(transparent));
  background: -webkit-linear-gradient(top left, black, transparent);
  background: -o-linear-gradient(top left, black, transparent);
  background: linear-gradient(to bottom right, black, transparent);
  width:100%;
  color:white;
  font-size:1em;
  z-index:1;
}


